import React from 'react'
import { Conexion } from '../Config/Coneccion'
import { conCompra, conOrden } from '../Models/Compra'
import { Estado, EstadoNull, GeneralFila, updateOrPush } from '../Models/General'
import { Proveedor, conProveedor, ProveedorNull } from '../Models/Compra'
import { ProductosContext, ProductosType } from './Producto'
import { formatoHora } from '../Utiles/Formatos'
interface State {
    estOrdenes: Estado
    estCompras: Estado
    estProveedores: Estado
    Ordenes: GeneralFila[]
    Compras: GeneralFila[]
    Proveedores: Proveedor[]
}
export interface ComprasType extends ProductosType, State {
    actualizar: () => void
    actCompra: () => void
    actOrden: () => void
    actProveedor: () => void
    findProveedor: (p: string) => Proveedor | undefined
    resetCompra: () => void
}
export const ComprasContext = React.createContext<ComprasType | null>(null)
class ComprasProvider extends React.Component<any, State> {
    interval: any;
    constructor(props: any) {
        super(props)
        this.state = {
            Ordenes: [],
            Compras: [],
            Proveedores: [],
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        }
    }
    render() {
        let seccionContext: ProductosType = this.context;
        return <ComprasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCompra: this.updaterCompra.bind(this),
            actOrden: this.updaterOrden.bind(this),
            actProveedor: this.updaterProveedor.bind(this),
            findProveedor: this.findProveedor.bind(this),
            resetCompra: this.reset.bind(this)
        }}>
            {this.props.children}
        </ComprasContext.Provider>
    }
    set() {
        this.updaterCompra();
        this.updaterOrden();
        this.updaterProveedor();
    }
    reset() {
        this.setState({
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        })
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (conCompra().state !== '')
            this.setState({ estCompras: { status: 3, hora: formatoHora(conCompra().state) } });
        if (conOrden().state !== '')
            this.setState({ estOrdenes: { status: 3, hora: formatoHora(conOrden().state) } });
        if (conProveedor().state !== '')
            this.setState({ estProveedores: { status: 3, hora: formatoHora(conProveedor().state) } });
        if (seccionContext.data.Permisos.length > 0) {
            if (seccionContext.data.Permisos.some(p => (p === 'COMPRA')))
                conCompra().cargarListaCache((Compras) => this.setState({ Compras }));
            conOrden().cargarListaCache((Ordenes) => this.setState({ Ordenes }));
            conProveedor().cargarListaCache((Proveedores) => this.setState({ Proveedores }));
            this.interval = setInterval(() => this.set(), 100)
        } else {
            setTimeout(() => this.init(), 1000);
        }
    }
    comBlock = true;
    updaterCompra() {
        if (this.comBlock) {
            this.comBlock = false;
            const { Compras } = this.state;
            conCompra().cambios((data: any[]) => {
                this.setState({ Compras: updateOrPush(data, Compras, conCompra()) })
                this.setState({ estCompras: { status: 3, hora: formatoHora(conCompra().state) } });
            }, () => this.comBlock = true)
        }
    }
    ordBlock = true;
    updaterOrden() {
        if (this.ordBlock) {
            this.ordBlock = false;
            const { Ordenes } = this.state;
            conOrden().cambios((data: any[]) => {
                this.setState({ Ordenes: updateOrPush(data, Ordenes, conOrden()) })
                this.setState({ estOrdenes: { status: 3, hora: formatoHora(conOrden().state) } });
            }, () => this.ordBlock = true)
        }
    }
    proBlock = true;
    updaterProveedor() {
        if (this.proBlock) {
            this.proBlock = false;
            const { Proveedores } = this.state;
            conProveedor().cambios((data: any[]) => {
                this.setState({ Proveedores: this.setCambioProveedores(data, Proveedores) })
                this.setState({ estProveedores: { status: 3, hora: formatoHora(conProveedor().state) } });
            }, () => this.proBlock = true)
        }
    }
    findProveedor(p: string) {
        const pro = this.state.Proveedores.find(Pro => Pro.codigo === p);
        if (pro !== undefined) return pro;
        return ProveedorNull;
    }
    arrayToProveedor(p: any[]): Proveedor[] {
        return p.map(a => {
            return {
                codigo: a[0],
                nombre: a[1],
                telefono: a[2],
                correo: a[3],
                contacto: a[4],
                direccion: a[5],
                estatus: a[6]
            }
        })
    }
    setCambioProveedores(data: Proveedor[], p: Proveedor[]) {
        const push = this.arrayToProveedor(data).filter((d => {
            const i = p.findIndex(p => p.codigo === d.codigo);
            if (i > -1) {
                p[i] = d;
                return false;
            }
            return true;
        }));
        const Proveedores = [...p, ...push];
        conProveedor().saveLista(Proveedores);
        return Proveedores;
    }
    componentWillUnmount() { clearInterval(this.interval); }
}
ComprasProvider.contextType = ProductosContext;
export default ComprasProvider;
export const SeccionConsumer = ComprasContext.Consumer;